<template>
  <div class="cursor" data-cursor>
    <div class="inner">
      <div class="circle"><span class="cursor_text"></span></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CustomCursor",
  props: {
    // targets: Array,
    // circleColor: String,
    // circleColorHover: String,
    // hoverSize: Number
  },
  data() {
    return {
      data: {
        mouse: {
        x: -200,
        y: -200,
        },
        current: {
          x: -200,
          y: -200,
        },
        last: {
          x: -200,
          y: -200,
        },
        ease: 0.1,
        dist: 50,
        fx: {
          diff: 0,
          acc: 0,
          velo: 0,
          scale: 0
        }
      }
    };
  },
  methods: {
    mousePos(e) {
        this.data.mouse.x = e.clientX
        this.data.mouse.y = e.clientY

        this.data.current.x = e.clientX
        this.data.current.y = e.clientY
    },
    run() {

        const mathCursor = {
            lerp: (a, b, n) => {
                return (1 - n) * a + n * b
            }
        }

        this.data.last.x = mathCursor.lerp(this.data.last.x, this.data.current.x, this.data.ease)
        this.data.last.y = mathCursor.lerp(this.data.last.y, this.data.current.y, this.data.ease)

        this.data.fx.diff = this.data.current.x - this.data.last.x
        this.data.fx.acc = this.data.fx.diff / window.innerWidth
        this.data.fx.velo = + this.data.fx.acc
        let scaleNum = 1 - Math.abs(this.data.fx.velo * 3)
        this.data.fx.scale = .7 < scaleNum ? scaleNum : .7

        this.el.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0) scale(${this.data.fx.scale})`

        this.raf()

    },
    raf() {
        this.rAF = requestAnimationFrame(this.run)
    },
    addListeners() {

        var element = this
        this.el = document.querySelector('[data-cursor]')
        this.hovers = document.querySelectorAll('[data-hover]')
        this.drags = document.querySelectorAll('[data-drag]')
        var el = document.querySelector('[data-cursor]')

        this.hovers.forEach(function (target) {


            target.addEventListener('mousemove', element.mousePos, { passive: true })
            target.addEventListener('mouseover', element.mousePos, { passive: true })


            target.addEventListener('mouseover', function () {

                

                var textContent = target.getAttribute('data-hover')
                var cursorStyle = target.getAttribute('data-style')
                el.querySelector('.cursor_text').innerHTML = textContent
                el.classList.add('is-active')

                if (cursorStyle == 'fill') {
                    el.classList.add('fill')
                } else {
                    el.classList.remove('fill')
                }

                

            });

            target.addEventListener('mouseout', function () {
                el.classList.remove('is-active')
                el.classList.remove('fill')
            });

        });


    },
    init() {
        this.addListeners()
        this.raf()
    },
  },
  mounted() {
        this.rAF = null
        this.targets = null

        this.run = this.run.bind(this)
        this.mousePos = this.mousePos.bind(this)


        this.init()
  }
};
</script>

<style lang="scss">
@media (any-pointer: fine) {
	.cursor {
		position: fixed;
		top: -6rem;
		left: -3.5rem;
		border-radius: 50%;
		pointer-events: none;
		z-index: 1000;
		height: 7rem;
		width: 7rem; 
	}
	.cursor .inner {
		width: 100%;
		height: 100%;
		position: relative; 
	}
	.cursor .inner .circle {
		width: 100%;
		height: 100%;
		border-radius: 50%;
		border: 1px solid transparent;
		opacity: 0;
		transition: transform .2s ease, opacity .35s ease, background-color .35s ease, border-color .35s ease;
		transform: scale(0.8);
		display: flex;
		justify-content: center;
		align-items: center; 
	}
	.cursor .inner .circle .cursor_text {
		display: block; 
	}
	.cursor .inner .line {
		position: absolute;
		width: 0px;
		height: 1px;
		background: #fff;
		right: 110%;
		top: 50%;
		transition: width .2s ease; 
	}
	.cursor .inner .line.line_right {
		left: 110%;
		right: auto; 
	}
	.cursor.is-active .inner .circle {
		border-color: #fff;
		opacity: 1;
        transform: scale(1); 
    }
	.cursor.fill .inner .circle {
		background-color: #fff;
		border-color: transparent; 
	}
	.cursor.fill .inner .circle .cursor_text {
		color: #000; 
	}
	.cursor.is-dragging {
		opacity: 0; 
	}
	.cursor.is-drag .inner .line {
		width: 5px; 
	}
	.cursor.is-drag.is-drag.is-dragging .inner .line {
		width: 20px; 
	}
}
</style>