<template>
  <li class="accordion__item">
    <div 
      class="accordion__trigger"
      :class="{'accordion__trigger_active': visible}"
      @click="open">

      <!-- This slot will handle the title/header of the accordion and is the part you click on -->
      <slot name="accordion-trigger"></slot>
      <svg class="open-icon" width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path :class="{'gsap-open': visible}" ref="gsapOpen" d="M1.27344 39.257L39.5018 1.02862M39.5018 1.02862L11.94 1.02734M39.5018 1.02862V27.1128" stroke="white"/>
      </svg>
    </div>

    <transition 
      name="accordion"
      @before-enter="beforeEnter"
      @enter="start"

      @before-leave="beforeLeave"
      @after-leave="end">

      <div class="accordion__content"
        v-show="visible">
        <div class="accordition-grid">
          <!-- This slot will handle all the content that is passed to the accordion -->
          <slot name="accordion-content"></slot>
        </div>

      </div>
      
    </transition>


  </li>
</template>


<script>
import gsap from "gsap";
import { MorphSVGPlugin } from "gsap/MorphSVGPlugin"
 
gsap.registerPlugin(MorphSVGPlugin)

export default {
  props: {},
  inject: ["Accordion"],
  data() {
    return {
      index: null
    };
  },
  computed: {
    visible() {
      return this.index == this.Accordion.active;
    }
  },
  // open (e, i) {
	// 		let el = this.$refs['accordion-body-' + i][0];

	// 		if (this.contents[i].active === false) {
	// 			this.contents[i].active = true;

	// 			TweenLite.to(el, 1, {
	// 				height: el.scrollHeight,
	// 				ease: Elastic.easeOut.config(1, 0.3)
	// 			});
	// 		} else {
	// 			this.contents[i].active = false;

	// 			TweenLite.to(el, 0.5, {
	// 				height: 0,
	// 				ease: Bounce.easeOut
	// 			});
	// 		}
	// 	},
  // created() {
  //   this.index = this.Accordion.count++;
  // }
  methods: {
    open() {
      if (this.visible) {
        this.Accordion.active = null;
      } else {
        this.Accordion.active = this.index;
          // let openIcon = gsap.timeline({paused: true})
          // openIcon.to(this.$refs.gsapOpen, {
          //   morphSVG: "M0.773438 38.8631L19.8876 19.749M39.0018 0.634766L19.8876 19.749M19.8876 19.749L0.773438 0.63479M19.8876 19.749L39.0018 38.8631",
          //   duration: 1,
          // })

          // openIcon.play()
      }
    },
    start(el) {
      el.style.height = el.scrollHeight + "px";



    },
    end(el) {
      el.style.height = "";



    },
    beforeEnter () {
          let openIcon = gsap.timeline({paused: true})
          openIcon.to(this.$refs.gsapOpen, {
            morphSVG: "M0.773438 38.8631L19.8876 19.749M39.0018 0.634766L19.8876 19.749M19.8876 19.749L0.773438 0.63479M19.8876 19.749L39.0018 38.8631",
            duration: .6,
          })

          openIcon.play()
    },
    beforeLeave () {
                let openIcon = gsap.timeline({paused: true})
          openIcon.to(this.$refs.gsapOpen, {
            morphSVG: "M1.27344 39.257L39.5018 1.02862M39.5018 1.02862L11.94 1.02734M39.5018 1.02862V27.1128",
            duration: .6,
          })

          openIcon.play()
    },
  },
  created() {
    this.index = this.Accordion.count++;
  }
};
</script>

<style lang="scss" scoped>
.accordion__item {

  padding: 80px 20px 80px 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
  display: grid;
  grid-template-columns: 40% 60%;
}
.accordion__trigger {
  display: flex;
  justify-content: space-between;
}
.open-icon {
  position: absolute;
  top: 80px;
  right: 0;
}
.accordion-enter-active,
.accordion-leave-active {
  will-change: height, opacity;
  transition: height 0.6s ease, opacity 0.6s ease;
  overflow: hidden;
}

.accordion-enter,
.accordion-leave-to {
  height: 0 !important;
  opacity: 0;
}
.accordition-grid {
  display: flex;
  flex-direction: column;
  margin-right: 7vw;
}
</style>
